@font-face {
  font-family: 'PT Sans';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:  local('PT Sans'),
        local('PT Sans Regular'),
        url('../fonts/PTSans-Regular.woff2') format(woff2),
        url('../fonts/PTSans-Regular.woff') format(woff),
}

@font-face {
  font-family: 'PT Sans';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src:  local('PT Sans'),
        local('PT Sans Bold'),
        url('../fonts/PTSans-Bold.woff2') format(woff2),
        url('../fonts/PTSans-Bold.woff') format(woff),
}

*, *::before, *::after {
  box-sizing: border-box;
}

.page {
  background-color: #f2f2f2;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  position: relative;
  max-width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  background-color: $white;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

.button {
  &:hover {
    background-color: $primary-hover;
  }

  &:active {
    background-color: $primary-active;
    color: rgba($color: $white, $alpha: 0.3);
  }

  &--disabled {
    background-color: #E5E5E5;
  }

  &:disabled {
    background-color: #E5E5E5;
  }
}
